@media only screen and (max-width:81.25em) {
    #page-sidebar {
        width: 100px
    }
    #page-content {
        margin-left: 100px;
        -webkit-transition: margin .5s ease;
        -o-transition: margin .5s ease;
        transition: margin .5s ease
    }
    #header-logo .logo-content-big {
        visibility: hidden;
        display: none
    }
    #close-sidebar, #page-sidebar li a.sf-with-ul:after, #sidebar-menu li.divider, #sidebar-menu li.header {
        display: none
    }
    #page-sidebar #sidebar-menu>li>a {
        line-height: 46px;
        height: 46px
    }
    #page-sidebar li a .glyph-icon {
        margin: 0 auto;
        float: none;
        height: 46px;
        line-height: 46px;
        width: 46px;
        font-size: 20px
    }
    #page-header {
        padding-left: 100px
    }
    #page-sidebar #sidebar-menu>li .bs-label {
        display: none
    }
    #page-sidebar.sidebar-hover {
        width: 300px
    }
    #page-sidebar #sidebar-menu>li>a span {
        visibility: hidden
    }
    #page-sidebar.sidebar-hover #sidebar-menu>li>a span {
        visibility: visible;
        transition-delay: .2s
    }
    #page-sidebar.sidebar-hover #sidebar-menu>li>a {
        line-height: 32px;
        height: 32px
    }
    #page-sidebar #sidebar-menu li .sidebar-submenu {
        display: none!important
    }
    #page-sidebar.sidebar-hover #sidebar-menu li.sfHover .sidebar-submenu {
        display: block!important
    }
    #page-sidebar.sidebar-hover #sidebar-menu li.divider, #page-sidebar.sidebar-hover #sidebar-menu li.header, #page-sidebar.sidebar-hover li a.sf-with-ul:after {
        display: block
    }
    #page-sidebar.sidebar-hover li a .glyph-icon {
        font-size: 16px;
        line-height: 31px;
        display: block;
        float: left;
        width: 32px;
        height: 32px;
        margin-right: 10px;
    }
    #header-logo .logo-content-small {
        display: block
    }
}

@media only screen and (min-width:56.25em) {
    #page-sidebar {
        -webkit-transition: width .5s ease;
        -o-transition: width .5s ease;
        transition: width .5s ease;
        visibility: visible
    }
    .closed-sidebar #page-sidebar {
        width: 100px
    }
    .closed-sidebar #page-content {
        margin-left: 100px;
        -webkit-transition: margin .5s ease;
        -o-transition: margin .5s ease;
        transition: margin .5s ease
    }
    .closed-sidebar #header-logo .logo-content-big {
        visibility: hidden
    }
    .closed-sidebar #close-sidebar {
        right: 50%;
        margin-right: -14px
