    }
    .closed-sidebar #page-sidebar.sidebar-hover #sidebar-menu>li>a span {
        visibility: visible;
        transition-delay: .2s
    }
    .closed-sidebar #page-sidebar.sidebar-hover #sidebar-menu>li>a {
        line-height: 32px;
