    }
    .closed-sidebar #page-sidebar #sidebar-menu li .sidebar-submenu {
        display: none!important
    }
    .closed-sidebar #page-sidebar.sidebar-hover #sidebar-menu li.sfHover .sidebar-submenu {
        display: block!important
    }
    .closed-sidebar #page-sidebar.sidebar-hover #sidebar-menu li.divider, .closed-sidebar #page-sidebar.sidebar-hover #sidebar-menu li.header, .closed-sidebar #page-sidebar.sidebar-hover li a.sf-with-ul:after {
        display: block
    }
    .closed-sidebar #page-sidebar.sidebar-hover li a .glyph-icon {
        font-size: 16px;
        line-height: 31px;
        display: block;
        float: left;
        width: 32px;
        height: 32px;
        margin-right: 10px;
        margin-left: 10px;
    }
    #header-logo, #page-sidebar.collapse, #theme-options {
        display: block;
    }
}

@media only screen and (max-width:760px) {
    #cloud-btn, #dashnav-btn, #header-nav-right #fullscreen-btn, #notifications-btn {
        display: none
    }
    .dropdown-menu {
        position: fixed;
        left: 0!important;
        width: 100%;
        border-radius: 0
    }
    #mobile-navigation {
        display: block;
        position: absolute;
        left: 10px;
        top: 13px;
        z-index: 555
    }
    #page-content-wrapper {
        padding-top: 0;
    }
    #page-header {
        padding-left: 0;
    }
    #page-content {
        margin-left: 0;
    }
    #page-sidebar {
        width: 100%;
        position: relative;
        display: none;
    }
    #page-sidebar #sidebar-menu>li .bs-label {
        display: none;
    }
    #page-sidebar #sidebar-menu>li>a span {
        visibility: visible;
        transition-delay: .2s;
    }
    #page-sidebar #sidebar-menu>li>a {
        line-height: 32px;
        height: 32px
    }
    #page-sidebar #sidebar-menu li .sidebar-submenu {
        display: none!important
    }
    #page-sidebar #sidebar-menu li.sfHover .sidebar-submenu {
        display: block!important
    }
    #page-sidebar #sidebar-menu li.divider, #page-sidebar #sidebar-menu li.header, #page-sidebar li a.sf-with-ul:after {
        display: block
    }
    #page-sidebar li a .glyph-icon {
        font-size: 16px;
        line-height: 31px;
        display: block;
        float: left;
        width: 32px;
        height: 32px;
        margin-right: 10px;
        margin-left: 0
    }
    .bootstrap-timepicker-widget {
        width: auto!important;
        left: 50px!important;
    }

}

@media only screen and (max-width:630px) {
    #page-header .user-account-btn>a.user-profile span {
        display: none
    }
    #page-header .user-account-btn .dropdown-menu:after, #page-header .user-account-btn .dropdown-menu:before {
        left: 56px
    }
    #page-sidebar {
        width: 100%;
        position: relative
    }
    #page-sidebar .scroll-sidebar {
        height: auto!important
    }
}

@media only screen and (max-width:545px) {
