.ui-tabs-nav {
    margin: 0;
    padding: 0;
    list-style: none;
    border-bottom: 1px solid transparent
}
.ui-tabs-nav li>a {
    line-height: 32px;
    height: 32px;
    margin-right: 5px;
    padding: 0 20px;
    border-bottom: 0
}
.ui-tabs-nav li {
    margin-bottom: -1px
}
.ui-tabs-panel {
    display: block;
    padding: 15px;
    background: 0 0
}
.ui-tabs-panel:last-child {
    border-bottom: 0!important
}
.ui-tabs-nav>li, .ui-tabs-nav>li>a {
    position: relative;
    float: left
}
.ui-tabs-nav>li>a .float-left.glyph-icon {
    float: none!important;
    margin-right: 5px
}
.ui-tabs-nav>li>a .float-right.glyph-icon {
    float: none!important;
    margin-right: 0;
    margin-left: 5px
}
.tabs-navigation>ul {
    margin: 0;
    padding: 0;
    border-width: 1px;
    border-style: solid;
    border-radius: 0;
    background: #fff
}
.tabs-navigation>ul li {
    margin: 0
}
.tabs-navigation>ul li>a {
    font-size: 20px;
    line-height: 64px;
    height: 64px;
    margin: 0!important;
    padding: 0 35px;
    border-right-width: 1px;
    border-right-style: solid;
    border-radius: 0;
    background: 0 0
}
.tabs-navigation>ul li.ui-tabs-active a {
    line-height: 64px;
    height: 64px;
    margin: 0!important;
    padding-bottom: 0;
    color: #000!important;
    background: #eff4f6
}
.tabs-navigation>ul li.ui-tabs-active a:hover, .tabs-navigation>ul li>a:hover {
    background: #eff4f6
}